import React from 'react';
import "./ContextMenu.scss";

export default function ContextMenu(props) {
  if (!props.isShow) {
    return null;
  }
  return (
    <div className="contextmenu" style={{ left: props.left, top: props.top }}>
        <ul>
          <li onClick={props.removeItem}></li>
          <li onClick={e => props.moveItem(e, 'top')}></li>
          <li onClick={e => props.moveItem(e, 'bottom')}></li>
          <li onClick={e => props.moveItem(e, 'up')}></li>
          <li onClick={e => props.moveItem(e, 'down')}></li>
        </ul>
    </div>
  )
}